        }
      );
      //=> "3 days ago"
    },
    getNotif(){
       sdb.collection("notifikasi").doc().select(`select * from notifikasi where id_user='${this.$store.state.users.id}' order by createdAt desc`).then(res => {
      // db.collection('notifikasi').where('id_user','==',this.$store.state.users.id).orderBy("createdAt", "desc").onSnapshot(res=>{
        let data = res
        let notiffalse=data.filter(e=>{
