        this.notification = data
        this.notification2 = notiffalse
        this.$forceUpdate()
      })
    }
  },
  computed:{
     distance() {
      return this.$datefns.distanceInWords(
        new Date("2018-12-25T03:21:12.941Z"),
        new Date(),
        {
          locale: id,
          includeSeconds: true
        }
      );
      return result;
    },
    format() {
      return this.$datefns.format(
